<template>
	<div>
		{{ this.$session.get('name') }}  !
		<FullCalendar
			defaultView="dayGridMonth"
			:plugins="calendarPlugins"
			:events="events"
			locale="ko"
			@dateClick="handleDateClick"
		/>
	</div>
</template>

<script>
import FullCalendar from '@fullcalendar/vue';
import dayGridPlugin from '@fullcalendar/daygrid';
import interactionPlugin from '@fullcalendar/interaction';
import '@fullcalendar/core/main.css';
import '@fullcalendar/daygrid/main.css';

export default {
	components: {
		FullCalendar, // make the <FullCalendar> tag available
	},
	data() {
		return {
			calendarPlugins: [interactionPlugin, dayGridPlugin],
			events: [],
			user: {
				user_idx: this.$session.get('user_idx'),
				name: this.$session.get('name'),
			},
		};
	},
	methods: {
		handleDateClick(arg) {
			var todayDate = this.$moment().format('YYYY-MM-DD'); // 
			var clickDate = this.$moment(arg.date).format('YYYY-MM-DD'); // 
			var todayDone = this.events.some(d => d.start == todayDate); // 

			if (todayDate != clickDate) {
				return alert('   !'); //      .
			}
			if (todayDone) {
				return alert(' !');
			} else {
				this.$http
					.post('/api/attendance/attCheck', {
						user: this.user,
					})
					.then(res => {
						if (res.data.success == true) {
							this.setAttPoint();
							alert(res.data.message);
							location.reload();
						}
					});
			}
		},
		//  
		setAttPoint() {
			this.$http.get(`/api/attendance/point/${this.$session.get('userid')}`).then(res => console.log(res));
		},
	},
	created() {
		//user_idx    
		this.$http
			.post('/api/attendance/attLog', {
				user: this.user,
			})
			.then(res => {
				this.events = res.data.attendance;
			});
	},
};
</script>
